import React from "react";
import {Helmet} from 'react-helmet'
import Bradcam from "../../components/Bradcam";
import ProjectDiscuss from "../../components/ProjectDiscuss";
import CustomerCarousel from "../../components/CustomerCarousel";
import PortfolioShow from "../../components/PortfolioShow";
const PortfolioPage = () => {
    return (
        <>
            <Helmet>
                <title>My Portfolio</title>
                <meta name="description" content="Phat's Portfolio Page" />
            </Helmet>
            <Bradcam title={'Portfolio'}/>
            <div className="portfolio_image_area dec_margin">
                <PortfolioShow/>
            </div>
            <CustomerCarousel/>
            <ProjectDiscuss/>

        </>
    )
};
export default PortfolioPage
